<script>
export default {
    name: "comisiones",
    data() {
        return {
            show_1: false,
            show_2: false,
            show_3: false,
            show_triangles: false,
            switch_height_val: false
        }
    },
    methods: {
        show_map() {
            this.show_1 = !this.show_1

            setTimeout(() => {
                this.show_2 = !this.show_2
            }, 300)

            setTimeout(() => {
                this.show_3 = !this.show_3
            }, 600)

            setTimeout(() => {
                this.show_triangles = !this.show_triangles
            }, 800)

            setTimeout(() => {
                this.switch_height()
                this.switch_height_val = !this.switch_height_val
            }, 1600)
        },
        switch_height() {
            setTimeout(() => {
                this.switch_height_val = !this.switch_height_val
            }, 100)
        }
    },
    watch: {
        $route: {
            immediate: true,
            handler() {
                if (this.$route.name == "comisiones") {
                    this.$router.replace({ name: "normativa" })
                }
            }
        }
    },
    mounted() {
        setTimeout(() => {
            this.show_map()
        }, 1000)
    }
}
</script>

