<script>
export default {
    name: "comisiones_index",
    data() {
        return {
            is_mobile: true
        }
    },
    methods: {
        handleResize() {
            const isMobile = window.matchMedia(
                "(max-width: 720px), (max-height: 410px)"
            )
            this.is_mobile = isMobile.matches ? true : false
        }
    },
    mounted() {
        window.addEventListener("resize", this.handleResize)
        this.handleResize()
    },
    beforeDestroy() {
        window.removeEventListener("resize", this.handleResize)
    }
}
</script>

